<template>
    <div class="content-wrapper col-sm-12">
        <div class="panel panel-flat">
            <div class="row">
                <div v-show="loader_icon" class="col-md-12" style="margin: 10px;">
                    <p class="text-center" style="margin-top: 5px;">
                        <i class="icon-spinner4 spinner position-left"></i>
                            PLEASE WAIT..
                    </p>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12" v-show="!loader_icon">
                    <div class="panel-body">
                        <div class="row">
                            <div class="col-md-6 col-xs-6 col-sm-6 col-lg-6">
                                <div class="form-group row">
                                    <label class="col-md-2 text-left">Branch</label>
                                    <div class="col-md-10">
                                        <select class="form-control" style="text-align-last: left;" v-model="selected_branch" :key="list_branches.length" @change="$emit('change', $event)">
                                            <option v-for="(row, i) in list_branches" :key="i" :value="row.branch_code">{{ row.branch_name }}</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3 col-xs-3 col-sm-3 col-lg-3">
                                <button class="btn btn-primary" style="width: 120px;" @click="searchBranch()">Search</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div v-show="loader_form" class="col-md-12" style="margin: 10px;">
                    <p class="text-center" style="margin-top: 5px;">
                        <i class="icon-spinner4 spinner position-left"></i>
                            PLEASE WAIT..
                    </p>
                </div>
            </div>
            <div class="row" v-show="loaded_form">
                <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12">
                    <div class="panel-heading" style="padding-top: 0px; padding-bottom:0px;">
                        <div class="row">
                            <div class="col-md-6">
                                <p class="h5">List of RS & BO Movements</p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-3">
                                <div class="form-group row">
                                    <div class="col-md-2"><label>From</label></div>
                                    <div class="col-md-10">
                                        <input type="date" class="form-control" v-model="date_from">
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="form-group row">
                                    <div class="col-md-2"><label>To</label></div>
                                    <div class="col-md-10">
                                        <input type="date" class="form-control" v-model="date_to">
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <button class="btn btn-primary" @click="searchBranch()">Search Data</button>
                                <button class="btn btn-danger" style="margin-left: 7px;" @click="searchClear()">Clear Search</button>
                            </div>
                        </div>
                    </div>
                    <div class="panel-body" style="padding-top: 0px;">
                        <div class="table-responsive">
                            <div class="form-group">
                                <input type="text" placeholder="Search" class="form-control" v-model="search" v-on:keyup="searchBranch" v-show="!hide_search">
                            </div>
                            <table class="table table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center"><strong>RS #</strong></th>
                                        <th class="text-center"><strong>RS DATE</strong></th>
                                        <th class="text-center"><strong>SUPPLIER CODE</strong></th>
                                        <th class="text-center"><strong>MOVEMENT TYPE</strong></th>
                                        <th class="text-center"><strong>MOVEMENT NO</strong></th>
                                        <th class="text-center"><strong>PROCESSED</strong></th>
                                        <th class="text-center"><strong>ACTION</strong></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(row, i) in list_rms" :key="i">
                                        <td class="text-center">{{ row.rs_id }}</td>
                                        <td class="text-center">{{ row.rs_date | date }}</td>
                                        <td class="text-center">{{ row.supplier_code }}</td>
                                        <td class="text-center">{{ row.movement_type }}</td>
                                        <td class="text-center">{{ row.movement_no }}</td>
                                        <td class="text-center">{{ (row.processed == 1) ? "POSTED" : "NOT POSTED" }}</td>
                                        <td class="text-center"><button class="btn btn-success" v-on:click="viewDetails(row.movement_no, row.rs_id)">View Details</button></td>
                                    </tr>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colspan="7" align="center">
                                            <paginate :page-count="page_count" :container-class="pagination" :prev-text="prev" :next-text="next" :click-handler="searchBranch"></paginate>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                            <!-- <pagination :total="total" :page-size="pageSize" :callback="pageChanged" :options="paginationOptions" nav-class="padding-10" ul-class="bg-color-red" li-class="txt-color-blue" class="text-center" style="margin-top:10px;">
                            </pagination> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import { handledError, tokenApi, apiUrl } from './../assets/custom/custom.js'

export default {
    data (){
        return {
            search: '',
            page_count: 0,
            currentSort: 'rs_date',
            currentSortDir: 'desc',
            prev: 'PREV',
            next: 'NEXT',
            pagination: 'pagination',
            loader_icon: false,
            loader_form: false,
            loaded_form: false,
            selected_branch: 'srsn',
            list_branches: [],
            list_rms: [],
            date_from: '',
            date_to: '',
            hide_search: false
        }
    },
    async mounted() {
        await this.fetch_branches()
    },
    methods: {
        async viewDetails(movement_no, rs_id) {
            this.$router.push({ name: 'Details', params: { branch_code: this.selected_branch, movement_no: movement_no, rs_id: rs_id } })
        },
        async searchClear() {
            this.date_from = ''
            this.date_to = ''
            this.search = ''
            await this.searchBranch()
        },
        async searchBranch(page = 1) {
            this.loader_form = true
            this.loaded_form = false
            this.list_rms = []
            this.page_count = 0
            try {
                if(this.date_from == '' || this.date_to == '') {
                    let query = {
                        params: {
                            branch_code: this.selected_branch,
                            p_page: page,
                            p_search: this.search,
                            p_currentSort: this.currentSort,
                            p_currentSortDir: this.currentSortDir
                        }
                    }
                    let result = await axios.get(apiUrl+'get_branch_details', query, tokenApi())
                    let data = result.data
                    this.list_rms = data.list_rms
                    this.page_count = data.page_count
                    this.hide_search = false
                } else {
                    let query = {
                        params: {
                            branch_code: this.selected_branch,
                            p_page: page,
                            p_search: this.search,
                            p_currentSort: this.currentSort,
                            p_currentSortDir: this.currentSortDir,
                            d_from: this.date_from,
                            d_to: this.date_to
                        }
                    }
                    let result = await axios.get(apiUrl+'get_branch_details_date', query, tokenApi())
                    let data = result.data
                    this.list_rms = data.list_rms
                    this.page_count = data.page_count
                    this.hide_search = true
                }
                this.loaded_form = true
            } catch(error) {
                handledError(error)
            }
            this.loader_form = false
        },
        async fetch_branches() {
            this.loader_icon = true //activate loading
            try {
                let result = await axios.get(apiUrl+'get_branches', tokenApi())
				let data = result.data
                this.list_branches = data.list_branches
            } catch (error) {
                handledError(error)
            }
            this.loader_icon = false //deactivate loading
        }
    }
}

</script>



<style scoped>
