import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login.vue'
import Dashboard from './components/Dashboard.vue'
import Details from './components/Details.vue'

Vue.use(Router)
export default new Router ({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Login',
            component: Login,
        },
        {
            path: '/dashboard',
            name: 'Dashboard',
            component: Dashboard
        },
        {
            path: '/view_details/:branch_code/:movement_no/:rs_id',
            name: 'Details',
            component: Details
        }
    ]
})