<template>
    <div class="content-wrapper col-sm-12">
        <div class="panel panel-flat">
            <div class="row" v-show="loader_icon">
                <div class="col-md-12" style="margin: 10px;">
                    <p class="text-center" style="margin-top: 5px;">
                        <i class="icon-spinner4 spinner position-left"></i>
                            PLEASE WAIT..
                    </p>
                </div>
            </div>
            <div class="row" v-show="!loader_icon">
                <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12">
                    <div class="panel-heading">
                        <strong>Movement No. {{ movement_no }} Details</strong>
                    </div>
                    <div class="panel-body" style="padding-top: 0px;">
                        <div class="row">
                            <div class="col-md-3">
                                <p>
                                    <strong>Supplier Details</strong>
                                </p>
                                <p>
                                    Supplier Name: {{ movement_header.ToDescription }}
                                </p>
                                <p>
                                    Supplier Code: {{ movement_header.VendorCode }}
                                </p>
                                <p>
                                    Supplier Address: {{ movement_header.ToAddress }}
                                </p>
                                <p>
                                    Contact Person: {{ movement_header.ContactPerson }}
                                </p>
                            </div>
                            <div class="col-md-3">
                                <p>
                                    <strong>Movement Details</strong>
                                </p>
                                <p>
                                    Movement Code: {{ movement_header.MovementCode }}
                                </p>
                                <p>
                                    Date Posted: {{ movement_header.PostedDate | date }}
                                </p>
                            </div>
                            <div class="col-md-3">
                                <p>
                                    <strong>Pickup Details</strong>
                                </p>
                                <p>
                                    Driver's Name: {{ pickup_details.tname }}
                                </p>
                                <p>
                                    Plate No.: {{ pickup_details.tplate_no }}
                                </p>
                            </div>
                            <div class="col-md-3">
                                <p>
                                    <strong>RS Inquiry</strong>
                                </p>
                                <p>
                                    RS #: {{ this.movement_no }}
                                </p>
                                <p>
                                    SA to BO Date: {{ this.rs_details.rs_date | date }}
                                </p>
                                <p>
                                    Supplier: {{ this.rs_details.supplier_code }}
                                </p>
                                <p>
                                    Extended: {{ this.total_amount }}
                                </p>
                                <p>
                                    Status: <strong :style="{'color':this.color_text}">{{ this.rs_cv_details }}</strong>
                                </p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12">
                                <h4 style="margin-top:0px;">List of Items</h4>
                                <div class="table-responsive">
                                    <table class="table table-bordered">
                                        <thead>
                                            <tr>
                                                <th class="text-center"><strong>PRODUCT ID</strong></th>
                                                <th class="text-center"><strong>BARCODE</strong></th>
                                                <th class="text-center"><strong>DESCRIPTION</strong></th>
                                                <th class="text-center"><strong>UOM</strong></th>
                                                <th class="text-center"><strong>UNIT COST</strong></th>
                                                <th class="text-center"><strong>QUANTITY</strong></th>
                                                <th class="text-center"><strong>AMOUNT</strong></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(row, i) in movement_details" :key="i">
                                                <td class="text-center">{{ row.ProductID }}</td>
                                                <td class="text-center">{{ row.barcode }}</td>
                                                <td class="text-center">{{ row.Description }}</td>
                                                <td class="text-center">{{ row.UOM }}</td>
                                                <td class="text-center">{{ row.unitcost }}</td>
                                                <td class="text-center">{{ row.qty }}</td>
                                                <td class="text-center">{{ (row.unitcost*row.qty).toFixed(2) }}</td>
                                            </tr>
                                        </tbody>
                                        <tfoot>
                                            <tr>
                                                <td colspan="6" class="text-right">
                                                    <strong>Total Quantity:</strong>
                                                </td>
                                                <td class="text-center">{{ total_quantity }}</td>
                                            </tr>
                                            <tr>
                                                <td colspan="6" class="text-right">
                                                    <strong>Total Amount:</strong>
                                                </td>
                                                <td class="text-center">{{ total_amount }}</td>
                                            </tr>
                                        </tfoot>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12">
                                <h4 style="margin-top:7px;">Attachments</h4>
                                <div v-show="pickup_details.timage == null" class="row text-center">
                                    <div class="col-md-3">
                                        <label><strong>Actual RS</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureActualRS_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>Invoice</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureInvoice_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>Driver's ID</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureDriversID_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>RS Slip</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureRSSlip_url">
                                    </div>
                                </div>
                                <div v-show="pickup_details.tpictureInvoice == null" class="row text-center">
                                    <div class="col-md-3">
                                        <label><strong>Actual RS</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureActualRS_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>Invoice</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureInvoice_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>Driver's ID</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureDriversID_url">
                                    </div>
                                    <div class="col-md-3">
                                        <label><strong>RS Slip</strong></label>
                                        <br>
                                        <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureRSSlip_url">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row" style="margin-top: 10px;">
                            <div class="col-md-12 col-xs-12 col-sm-12 col-lg-12 text-right">
                                <button class="btn btn-success" :disabled="loader_download" @click="export_excel()">EXPORT TO EXCEL</button>
                                <p v-show="loader_download" class="text-right" style="margin-top: 5px;">
                                    <i class="icon-spinner4 spinner position-left"></i>
                                        PLEASE WAIT..
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import FileSaver from 'file-saver';
import { handledError, tokenApi, apiUrl, imageUrl } from './../assets/custom/custom.js'

export default {
    data (){
        return {
            branch_code: '',
            movement_no: '',
            rs_id: '',
            loader_icon: true,
            loader_download: false,
            pickup_details: [],
            movement_header: [],
            movement_details: [],
            rs_details: [],
            rs_cv_details: [],
            total_quantity: 0,
            total_amount: 0,
            pictureInvoice_url: '',
            pictureRSSlip_url: '',
            pictureDriversID_url: '',
            pictureActualRS_url: '',
            color_text: ''
        }
    },
    async created() {
        this.branch_code = this.$route.params.branch_code
        this.movement_no = this.$route.params.movement_no
        this.rs_id = this.$route.params.rs_id
    },
    async mounted() {
        await this.fetch_branches()
        await this.rms_details()
        await this.rscv_details()
    },
    methods: {
        async rscv_details() {
            try {
                let query = {
                        branch_code: this.branch_code,
                        movement_no: this.movement_no
                }
                let result = await axios.post(apiUrl+'get_rs_cv_no', query , tokenApi())
				let data = result.data
                if(data.cv_id != 0 && data.cv_amount != 0) {
                    this.rs_cv_details = 'DEDUCTED'
                    this.color_text = 'blue'
                } else if(data.cv_id != 0 && data.cv_amount == 0) {
                    this.rs_cv_details = 'CV VOIDED'
                    this.color_text = 'red'
                } else {
                    this.rs_cv_details = 'NOT DEDUCTED'
                    this.color_text = 'red'
                }
            } catch (error) {
                handledError(error)
            }
        },
        async rms_details() {
            try {
                let query = {
                        branch_code: this.branch_code,
                        rs_id: this.movement_no,
                        supplier_code: this.movement_header.VendorCode
                }
                let result = await axios.post(apiUrl+'get_rms_details', query , tokenApi())
				let data = result.data
                this.rs_details = data
            } catch (error) {
                handledError(error)
            }
        },
        async fetch_branches() {
            this.loader_icon = true //activate loading
            try {
                let query = {
                    params: {
                        branch_code: this.branch_code,
                        movement_no: this.movement_no,
                        rs_id: this.rs_id
                    }
                }
                let result = await axios.get(apiUrl+'get_movement_details', query , tokenApi())
				let data = result.data
                this.pickup_details = data.res[0].pickup_details
                this.movement_header = data.res[0].movement_header
                this.movement_details = data.res[0].movement_details
                this.pictureInvoice_url = (data.res[0].pickup_details.timage == null) ? imageUrl+`${data.res[0].branch_name}/${this.rs_id}/`+data.res[0].pickup_details.tpictureInvoice : imageUrl+`${data.res[0].branch_name}/${this.rs_id}/`+data.res[0].pickup_details.timage
                this.pictureRSSlip_url = imageUrl+`${data.res[0].branch_name}/${this.rs_id}/`+data.res[0].pickup_details.tpictureRSSlip
                this.pictureDriversID_url = imageUrl+`${data.res[0].branch_name}/${this.rs_id}/`+data.res[0].pickup_details.tpictureDriversID
                this.pictureActualRS_url = imageUrl+`${data.res[0].branch_name}/${this.rs_id}/`+data.res[0].pickup_details.tpictureActualRS
            } catch (error) {
                handledError(error)
            }
            this.loader_icon = false //deactivate loading
        },
        async export_excel() {
            this.loader_download = true
            try {
                let query = {
                    pickup_details: this.pickup_details,
                    movement_header: this.movement_header,
                    movement_details: this.movement_details,
                    total_quantity: this.total_quantity,
                    total_amount: this.total_amount,
                    rs_id: this.rs_id,
                    movement_no: this.movement_no
                }

                let result = await axios.post(apiUrl+'export_movement_details', query, tokenApi())
                let data = result.data
                setTimeout(() => {
                    if(data.res != null || data.res != "") {
                        FileSaver.saveAs(data.res)
                        this.loader_download = false
                    }
                }, 3000)
            } catch (error) {
                handledError(error)
            }
        }
    },
    watch: {
        movement_details: {
            handler: function() {
                this.movement_details.forEach(p => {
                    this.total_quantity = parseFloat(this.total_quantity) + parseFloat(p.qty)
                    this.total_amount = (parseFloat(this.total_amount) + (parseFloat(p.unitcost)*parseFloat(p.qty))).toFixed(3)
                })
            }, deep: true
        },
    }
}

</script>

